.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App {
  background-image: url("./Assets/img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header {
  padding: 60px;
  text-align: center;
  color: white;
  font-size: 100px;
}

.reply
{
  font-size: 60px;
  padding: 60px;
}

img:hover
{
  box-shadow: 0px 0px 15px #fff;
}
